.code-display {
	min-width: 100%;
	background: #272727;
	resize: none;
	outline: none;
	border: none;
	transition: color 1s;
	padding: 0.5em;
	/*white-space: nowrap;*/
	box-sizing: border-box; 

	color: white;
	font-family: monospace;

	/*border: 0.5em solid #E8E4E4;*/
	overflow: hidden;
} 

