/* =================== Body Scrollbar */
.code-display::-webkit-scrollbar { 
  height: 16px;
  width: 16px;
  background: transparent; 
}

.code-display::-webkit-scrollbar-corner {
	background: #a7a7a7;
}
 
.code-display::-webkit-scrollbar-thumb { 
	background: #a7a7a7;  
}

.code-display::-webkit-scrollbar-thumb:hover { 
	background: #d6d1d1;
}